import link from "tables/link";

/**
 * Before
 * @memberof Word
 * @return {string} HTML as string
 */
export function getHelperWordsBefore() {
  let text = "";
  /* Nouns et al. */
  if (
    this.is("noun") ||
    this.is("adjective") ||
    this.is("past participle") ||
    this.is("pronoun") ||
    this.is("personal pronoun") ||
    this.is("article") ||
    this.is("reflexive pronoun")
  ) {
    if (this.is("nominative", "singular")) {
      text = "h√©r er";
      if (this.getBaseWord() === "√æ√∫") {
        text = "h√©r ert";
      }
    }
    if (this.is("nominative", "plural")) {
      text = "h√©r eru";
      if (this.getBaseWord() === "√©g") {
        text = "h√©r erum";
      }
      if (this.getBaseWord() === "√æ√∫" || this.getBaseWord() === "√æ√©r") {
        text = "h√©r eru√∞";
      }
    }
    if (this.is("accusative")) {
      text = "um";
    }
    if (this.is("dative")) {
      text = "fr√°";
    }
    if (this.is("genitive")) {
      text = "til";
    }
    text = link("helper words for declension", text);
  } else if (this.is("verb") && !this.is("question form")) {
  /* Verbs */
    if (this.is("infinitive")) {
      text = "a√∞";
    }
    if (this.is("supine")) {
      text = "√©g hef";
    }
    if (this.is("present participle")) {
      text = "hann er";
    }
    if (this.is("subjunctive")) {
      if (this.is("present tense")) {
        text = "√©g held a√∞ ";
      }
      if (this.is("past tense")) {
        text = "√©g h√©lt a√∞ ";
      }
    }
    if (this.is("singular")) {
      if (this.is("1st person")) {
        text += this.dependingOnSubject("√©g", "mig", "m√©r", "m√≠n", "√æa√∞");
      }
      if (this.is("2nd person")) {
        text += this.dependingOnSubject("√æ√∫", "√æig", "√æ√©r", "√æ√≠n", "√æa√∞");
      }
      if (this.is("3rd person")) {
        text += this.dependingOnSubject(
          "h√∫n",
          "hana",
          "henni",
          "hennar",
          "√æa√∞"
        );
      }
    }
    if (this.is("plural")) {
      if (this.is("1st person")) {
        text += this.dependingOnSubject(
          "vi√∞",
          "okkur",
          "okkur",
          "okkur",
          "√æa√∞"
        );
      }
      if (this.is("2nd person")) {
        text += this.dependingOnSubject(
          "√æi√∞",
          "ykkur",
          "ykkur",
          "ykkur",
          "√æa√∞"
        );
      }
      if (this.is("3rd person")) {
        text += this.dependingOnSubject("√æ√¶r", "√æ√¶r", "√æeim", "√æeirra", "√æa√∞");
      }
    }
  }
  return text;
}

/**
 * After
 * @memberof Word
 * @return {string} HTML string
 */
export function getHelperWordsAfter() {
  let text = "";
  let addSpace = true;

  /* Nouns */
  if (this.is("noun", "with definite article")) {
    if (this.is("singular")) {
      if (this.is("nominative")) {
        text = this.dependingOnGender("minn", "m√≠n", "mitt");
      }
      if (this.is("accusative")) {
        text = this.dependingOnGender("minn", "m√≠na", "mitt");
      }
      if (this.is("dative")) {
        text = this.dependingOnGender("m√≠num", "minni", "m√≠nu");
      }
      if (this.is("genitive")) {
        text = this.dependingOnGender("minns", "minnar", "m√≠ns");
      }
    } else if (this.is("plural")) {
      if (this.is("nominative")) {
        text = this.dependingOnGender("m√≠nir", "m√≠nar", "m√≠n");
      }
      if (this.is("accusative")) {
        text = this.dependingOnGender("m√≠na", "m√≠nar", "m√≠n");
      }
      if (this.is("dative")) {
        text = this.dependingOnGender("m√≠num", "m√≠num", "m√≠num");
      }
      if (this.is("genitive")) {
        text = this.dependingOnGender("minna", "minna", "minna");
      }
    }
    text = link("helper words for the article", text);
  } else if (this.isAny("adjective", "past participle", "article")) {

  /* Adjectives & past participle */
    if (!this.is("weak declension") /*&& !this.is('article')*/) {
      if (this.is("singular")) {
        if (this.is("nominative")) {
          text = this.dependingOnGender("ma√∞ur", "kona", "barn");
        }
        if (this.is("accusative")) {
          text = this.dependingOnGender("mann", "konu", "barn");
        }
        if (this.is("dative")) {
          text = this.dependingOnGender("manni", "konu", "barni");
        }
        if (this.is("genitive")) {
          text = this.dependingOnGender("manns", "konu", "barns");
        }
      } else if (this.is("plural")) {
        if (this.is("nominative")) {
          text = this.dependingOnGender("menn", "konur", "b√∂rn");
        }
        if (this.is("accusative")) {
          text = this.dependingOnGender("menn", "konur", "b√∂rn");
        }
        if (this.is("dative")) {
          text = this.dependingOnGender("m√∂nnum", "konum", "b√∂rnum");
        }
        if (this.is("genitive")) {
          text = this.dependingOnGender("manna", "kvenna", "barna");
        }
      }
    } else {
      if (this.is("singular")) {
        if (this.is("nominative")) {
          text = this.dependingOnGender("ma√∞urinn", "konan", "barni√∞");
        }
        if (this.is("accusative")) {
          text = this.dependingOnGender("manninn", "konuna", "barni√∞");
        }
        if (this.is("dative")) {
          text = this.dependingOnGender("manninum", "konunni", "barninu");
        }
        if (this.is("genitive")) {
          text = this.dependingOnGender("mannsins", "konunnar", "barnsins");
        }
      } else if (this.is("plural")) {
        if (this.is("nominative")) {
          text = this.dependingOnGender("mennirnir", "konurnar", "b√∂rnin");
        }
        if (this.is("accusative")) {
          text = this.dependingOnGender("mennina", "konurnar", "b√∂rnin");
        }
        if (this.is("dative")) {
          text = this.dependingOnGender("m√∂nnunum", "konunum", "b√∂rnunum");
        }
        if (this.is("genitive")) {
          text = this.dependingOnGender("mannanna", "kvennanna", "barnanna");
        }
      }
    }
  } else if (this.is("verb")) {

  /* Verbs */
    if (this.is("present tense")) {
      text = "(√≠ dag)";
    }
    if (this.is("past tense")) {
      text = "(√≠ g√¶r)";
    }
    if (this.is("clipped imperative")) {
      text = "√æ√∫!";
    } else if (this.is("imperative")) {
      text = "!";
      addSpace = false;
    }
    if (this.is("question form")) {
      text = "?";
      addSpace = false;
    }
  }

  /* Add space between word, except for exclamation marks */
  if (addSpace && text) {
    return " " + text;
    // return '\u202F\u202F' + text
  } else {
    return text;
  }
}
